import React from "react";
import { Link } from "react-router-dom";
import "./NoEncontrado.css";

const NoEncontrado = () => {
  return (
    <div className="contenedor">
      <div className="contenedor-contenido">
        <img
          className="imagen"
          src="/header/Logo.svg"
          alt="Logo Viviendas La Esperanza"
        />
        <h3 className="titulo">Pgina no encontrada</h3>
        <p className="parrafo">
          La url de la pgina que solicitaste no es correcta. Hac clic en el
          botn para regresar a la pgina de inicio o buscar nuestros modelos.
        </p>
        <Link className="button" to="/">
          Volver al inicio
        </Link>
      </div>
    </div>
  );
};

export { NoEncontrado };